
.headerwrap{
  width:1920px;
}
.gnbwrap{
  /* width:1653px; */

  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding:0 140px;
  width: 1640px;
  .logowrap{
    display: flex;
    align-items: center;
    color: #000;
    font-size: 16px;
    line-height: 60px; /* 375% */
    .countrytime{
      display: flex;
      align-items: center;
      font-weight: 600;
      // height: 60px;
      // line-height: 65px;
      padding-left: 62px;
      &::after{
        content: '/';
        padding: 0 10px;
      }
      img{
        width: 36px;
        height: 36px;
      }
      span{
        padding-left: 8px;
      }
    }
    .time{
      height: 60px;
      line-height: 62px;
    }
  }
  .userinfo{
    display: flex;
    align-items: center;
    .headersearchwrap{
      position: relative;
      padding:0 20px;
      &::after{
        content: "";
        position: absolute;
        width: 1px;
        height: 16px;
        top:4px;
        left:0;
        background-color:#c5cebc;
      }
    }
    .headersearchbtn{
      position: relative;
      border: none;
      background-color: transparent;
      width:28px;
      height: 28px;
      &::after{
        content: '';
        position: absolute;
        top:0;
        left:0;
        width: 18px;
        height: 18px;
        border-radius: 100px;
        border: 2px solid #767472;
        background-color: #F0EBE5;
      }
      &::before{
        content: "";
        position: absolute;
        width: 17px;
        height: 17px;
        top:26px;
        left:8px;
        border-top: 2px solid #767472;
        // border-right: solid 2px #515151;
        transform: translateY(-50%) rotate(-320deg);
      }
    }
    .headernotiwrap{
      position: relative;
      padding:0 20px;
      .alarm_menu{
        position: absolute;
        width: 142px;
        border: 1px solid #4C4B48;
        position:absolute;
        background: #FFF;
        box-shadow: 0px 6px 10px 0px rgba(0, 0, 0, 0.15);
        top:40px;
        left:50%;
        transform: translateX(-50%);
        border-radius:10px;
        padding:36px 20px;
        text-align:center;
        z-index:3;
        li{
          color: #000;
          font-family: "LG EI Text TTF";
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 36px; /* 225% */
          text-align: left;
          &:last-child{
            padding:0;
          }
          a{
            color: #000;
            text-decoration: none;
          }
        }
        &::after{
          position:absolute;
          content:'';
          width: 0px;
          height: 0px;
          border-right: 12px solid #fff;
          border-top: 7px solid transparent;
          border-bottom: 7px solid transparent;
          transform: rotate(90deg);
          left: 49%;
          top:-11px;
        }
        &::before{
          position:absolute;
          content:'';
          width: 0px;
          height: 0px;
          border-right: 12px solid #4C4B48;
          border-top: 7px solid transparent;
          border-bottom: 7px solid transparent;
          transform: rotate(90deg);
          left: 49%;
          top:-13px;
        }
      }
      &::after{
        content: "";
        position: absolute;
        width: 1px;
        height: 16px;
        top:4px;
        left:0;
        background-color:#c5cebc;
      }
      span{
        position: absolute;
        right:18px;
        top:2px;
        display: inline-block;
        width:14px;
        height: 14px;
        line-height: 14px;
        text-align: center;
        border-radius: 100px;
        font-weight:600;
        background-color: #EB1918;
        color:#fff;
        font-size: 12px;
      }
      .headernotibtn{
        background: url('../assets/noti_icon_08.png') no-repeat ;
        background-size: cover; 
        width:28px;
        height:28px;
        position: relative;
        border: none;
        opacity: 0.8;
      }
    }
    .countryselect{
      width: 90px;
      height:30px;
      line-height: 30px;
      display: flex;
      cursor: pointer;
      position: relative;
      &::before{
        content: "";
        position: absolute;
        width: 1px;
        height: 16px;
        top:4px;
        left:0;
        background-color:#c5cebc;
      }
      &::after{
        content: "";
        position: absolute;
        width: 1px;
        height: 16px;
        top:4px;
        right:0;
        background-color:#c5cebc;
      }
      .countryvalue{
        width: 50px;
        padding:0 20px;
        position: relative;
        &::after{
          content: "";
          position: absolute;
          width: 8px;
          height: 8px;
          right:20px;
          top:10px;
          background-color: #F0EBE5;
          border-top: solid 2px #9b9b9b;
          border-right: solid 2px #9b9b9b;
          transform: translateY(-50%) rotate(-225deg);
        }
      }
    }
    .username{
      color: #262626;
      text-align: center;
      font-size: 16px;
      font-weight: 600;
      line-height: 60px; /* 375% */
      padding-left: 20px;
      color: #262626;
      text-align: center;
      font-family: "LG EI Text TTF";
    }
    .usertype{
      font-size: 16px;
      font-weight: 400;
      padding: 0 10px 0 8px;
    }
    .lockbtn{
      background: url(../assets/lock_icon.png) no-repeat;
      width:28px;
      height:28px;
      background-size: cover;
      margin-top: -4px;
    }
    .usericon{
      width:30px;
      height: 30px;
      margin-left: 8px;
      img{
        width:30px;
        height: 30px;
        margin-top: -2px;
      }
    }
  }
}
.logo{
  background: url('../assets/bi.png') no-repeat;
  background-size: cover; 
  width:148px;
  height:36px;
}
.menuwrap{
  // height: 46px;
  // line-height: 46px;
  position: relative;
  z-index:2;
  background: #4C4B47;
  .menuinner {
    // margin: 0 auto;
    // width:800px;
    > ul{
      width:100%;
      display: flex;
      justify-content: center;
      // gap: 53.333px;
      flex-shrink: 0;
      color:#fff;
      height: 45.333px;
      li{width:180px;
        height: 42px;
        line-height: 42px;
        border-bottom: 4px solid transparent;
        
        a{
          font-size: 20px;
          height: 42px;
          line-height: 42px;
          display: inline-block;
          color:#fff;
          text-decoration: none;
          &:hover{
            border-bottom: 4px solid #EB1918;
          }
        }
      }
    }
  }
  .submenuwrap{
    position: absolute;
    z-index: 999 !important ;
    background: #fff;
    min-width: 790px;
    width: 1920px;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    padding: 40px 0;
    box-shadow: 0px 10px 2px 0px rgba(0, 0, 0, 0.2);
    // &::before{
    //   content: '';
    //   position: fixed; 
    //   top: 0; 
    //   left: 0px; 
    //   width: 100%; 
    //   height: 100%; 
    //   z-index: -1 !important; 
    //   opacity: 0.3; 
    //   background-color: rgb(0, 0, 0);
    // }
    > ul{
      width:180px;
      gap: 53.333px;
      flex-shrink: 0;
      color:#fff;
      li{
        // height: 42px;
        // line-height: 42px;
        border-bottom: 4px solid transparent;
        padding-bottom: 20px;
        a{
          color: #4C4B48;
          text-align: center;
          font-family: "LG EI Text TTF";
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 16px; /* 100% */
          letter-spacing: -0.16px;
          text-decoration: none;
          &:hover{
            color: #EB1918;
          }
        }
      }
    }
  }
  
}
