}
@font-face {
  font-family: "LG EI Text TTF";
  font-weight: 200;
  src: url("./fonts/LGEIHeadlineTTF-Thin.ttf") format("truetype");
}
@font-face {
  font-family: "LG EI Text TTF";
  font-weight: 300;
  src: url("./fonts/LGEIHeadlineTTF-Light.ttf") format("truetype");
}
@font-face {
  font-family: "LG EI Text TTF";
  font-weight: 400;
  src: url("./fonts/LGEITextTTF-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "LG EI Text TTF";
  font-weight: 500;
  src: url("./fonts/LGEIHeadlineTTF-Semibold.ttf") format("truetype");
}
@font-face {
  font-family: "LG EI Text TTF";
  font-weight: 600;
  src: url("./fonts/LGEITextTTF-Bold.ttf") format("truetype");
}


button{cursor: pointer;}
.contbox{
  /* width:1782px; */
  border-radius: 8px;
  background: #FFF;
  box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.06);
  margin: 0 auto;
  

}
html, body {
  background: #F0EBE5;
  font-family: "LGEITextTTF-Light" !important;
  
}
.wrapper{width:1920px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
